				uni.showToast({
					title: '第一个HScode不能为空',
					icon: 'none',
					duration: 800
				});
				return false;
			} else if (this.searchs == '' || this.searchs == undefined || this.searchs == null) {
				uni.showToast({
					title: '第二个HScode不能为空',
					icon: 'none',
					duration: 800
				});
				return false;
			}

			//  接口传参	 type=KnowHScode 已知hscode查询
			uni.navigateTo({
				url:
					'../chaxunjieguo/chaxunjieguo?type=KnowHScode&city_one_name=' +
